import { graphql } from "gatsby"
import { MDXRenderer } from 'gatsby-plugin-mdx';
import Helmet from "react-helmet"
import React from 'react';
import dateFormat from 'dateformat';

import SEO from "../../components/SEO"
import Layout from '../../components/Layout';

const BlogSEO = ({ post, site }) => {
  const meta = [
    { property: 'og:type', content: 'article' },
    { property: 'article:author', content: post.frontmatter.author || site.siteMetadata.author },
  ];

  if (post.excerpt) {
    meta.push({ name: 'description', content: post.excerpt });
    meta.push({ property: 'og:description', content: post.excerpt });
  }
  if (post.frontmatter.date) {
    meta.push({ property: 'article:published_time', content: post.frontmatter.date });
  }
    console.log(post)
  if (post.frontmatter.tags) {
    meta.push({ property: 'article:tag', content: post.frontmatter.tags });
  }

  return (
    <>
      <Helmet
        meta={meta}
      />
      {/*<SchemaOrg />*/}
    </>
  )
};

export default ({ data }) => {
  const { frontmatter, body, excerpt } = data.mdx;
  console.log(data.mdx)
  return (
    <Layout>
      <SEO title={frontmatter.title} description={excerpt} />
      <BlogSEO post={data.mdx} site={data.site} />
      <h1>{frontmatter.title}</h1>
      <time
        dateTime={dateFormat(frontmatter.date, 'yyyy-mm-dd')}
      >
        {dateFormat(frontmatter.date, 'dddd, mmmm dS, yyyy')}
      </time>
      <MDXRenderer>{body}</MDXRenderer>
    </Layout>
  );
};

export const query = graphql`
  query PostsBySlug($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      excerpt(pruneLength: 150)
      frontmatter {
        title
        date
        author
        tags
      }
    }
    site {
      siteMetadata {
        author
      }
    }
  }
`;
